import React from 'react'
import MessageGroup from './MessageGroup'

const styles = {
  header: {
    color: "white",
    fontFamily: "'Great Vibes', cursive",
    letterSpacing: "2px",
    fontSize: "2rem",
  }
}

const ChatBox = () => {
  const messagesList = [
    {
      type: "sent",
      messages: ["You waved at Ngn!"]
    },
    { 
      type: "received",
      messages: ["You and Ngn waved at each other!"]
    },
    {
      type: "sent",
      messages: ["Ngn c nh hc  trung tn Light lu di hng d"]
    },
    { 
      type: "received",
      messages: ["Ngn cha bit na", "M chc hc gi ti tt trc ", "Qua tt tnh tip"]
    },
    {
      type: "sent",
      messages: ["Ti nh nh Ngn ch qua thy t dm "]
    },
    { 
      type: "received",
      messages: [" c nha", ""]
    },
    {
      type: "sent",
      messages: ["Mn Ngn nha hihi"]
    },
    { 
      type: "received",
      messages: ["C g u :3"]
    },
  ]

  return (
    <>
      <h2 style={styles.header}>First  chats</h2>
      <div className="messages-container">
        {messagesList.map((messages, index)=><MessageGroup key={index} type={messages.type} messageList={messages.messages}/>)}
      </div>
    </>
  )
}

export default ChatBox